<template>
	<div id="show-blogs" v-theme:column="'wide'">
		<h2>博客总览</h2>
		<input type="text" v-model="search" placeholder="搜索">
		<div class="single-blog" v-for="blog in filteredBlogs" :key="blog.title">
			<router-link :to="'/blog/' + blog.id">
				<h3 v-rainbow>{{blog.title | toUpperCase}}</h3>
				<article>{{blog.content | snippet}}</article>
			</router-link>				
		</div>
	</div>
</template>
<script type="text/javascript">
	// import axios from 'axios'
	export default {
		name: 'NewShowBlogs',
		data(){
			return {
				blogs: [],
				search: ''
			}
		},
		created(){
			// this.$http.get('https://wd2091622437tspsax.wilddogio.com/post.json')
			// .then((data) =>{
			// 	// this.blogs = data.body.slice(0,10);
			// 	return data.json()
			// })
			 
			this.axios.get('/post.json')			
			.then((response) =>{
				var blogsArray = [];
				for(var key in response.data){
					response.data[key].id = key
					blogsArray.push(response.data[key])
				}
				this.blogs = blogsArray
			})
		},
		computed: {
			filteredBlogs(){
				return this.blogs.filter((item) =>{
					return item.title.match(this.search)
				})
			}
		},
		directives: {
			'rainbow': {
				bind: function(el,binding){
					el.style.color = '#' + Math.random().toString(16).substring(2,8)
				}
			},
			'theme': {				
				bind(el,binding){
					if(el.binding = 'wide'){
						el.style.maxWidth = '1366px'
					}else if(el.binding = 'narrow'){
						el.style.maxWidth = '668px'
					}
					if(binding.arg == 'column'){
						el.style.background = '#6677cc'
						el.style.padding = '50px'
					}
				}
			}
		},
		filters: {
			toUpperCase: function(value){
				return value.toUpperCase()
			},
			snippet: function(value){
				return value.slice(1,100) + '...'
			}
		}
		
	}
</script>
<style>
	
</style>
<style type="text/css">
#show-blogs{
	max-width: 1080px;
	margin: 0 auto;
}

.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
  border: 1px dotted #aaa;
}

#show-blogs a{
  color: #444;
  text-decoration: none;
}

input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}

</style>


