<template>
	<div id="add-blog">
		<h2>写博客</h2>
		<form v-if="!isSubmmited">
			<label for="">博客标题</label>
			<input type="text" v-model="blog.title">
			<label for="">博客标题</label>
			<textarea name="" v-model="blog.content"></textarea>

			<div id="checkboxes">
				<label for="">vue.js</label>
				<input type="checkbox" value="vue.js" v-model="blog.categories">
				<label for="">react.js</label>
				<input type="checkbox" value="react.js" v-model="blog.categories">
				<label for="">anglar.js</label>
				<input type="checkbox" value="anglar.js" v-model="blog.categories">
				<label for="">node.js</label>
				<input type="checkbox" value="node.js" v-model="blog.categories">
			</div>
			<div>
				<label for="">作者</label>
				<select v-model="blog.author">
					<option v-for="author in authors" :key="author">{{author}}</option>
				</select>
			</div>
			<button @click.prevent="post">添加博客</button>
		</form>
		<br>
		<div v-if="isSubmmited"><h3>你已经成功添加博客!</h3></div>
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题：{{blog.title}}</p>
			<p>博客内容</p>
			<p>{{blog.content}}</p>
			<p>博客分类</p>
			<ul>
				<li v-for="categorie in blog.categories" :key="categorie">{{categorie}}</li>
			</ul>
			<p>博客作者：{{blog.author}}</p>
		</div>
	</div>
</template>
<script type="text/javascript">
	// import axios from 'axios'
	export default {
		name: 'add-blog',
		data(){
			return {
				blog: {
					title: '',
					content: '',
					categories: [],
					author: ''
				},
				authors: ['kobe','james','wade'],
				isSubmmited: false
			}
		},
		methods: {
			post: function(){
				// this.$http.post('https://wd2091622437tspsax.wilddogio.com/post.json',this.blog)
				this.axios.post('/post.json',this.blog)				
				.then((data) =>{
					this.isSubmmited = true
				})
			}
		}
		
	}
</script>
<style>
	
</style>
<style type="text/css">
	#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label{
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 10px;
}

textarea{
  height: 200px;
}
#checkboxes{
	margin-top: 30px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}

#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}

button{
  display: block;
  margin: 20px auto;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3{
  margin-top: 10px;
}

</style>


