import Vue from 'vue'
import VueRouter from 'vue-router'
import ShowBlogs from '@/components/NewShowBlogs'
import AddBlog from '@/components/AddBlog'
import SingleBlog from '@/components/SingleBlog'
import EditBlog from '@/components/EditBlog'

Vue.use(VueRouter)

let router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'ShowBlogs',
      component: ShowBlogs
    },
    {
    	path: '/add',
    	name: 'AddBlog',
    	component: AddBlog
    },
    {
      path:"/blog/:id",
      name: 'SingleBlog',
      component: SingleBlog
    },
    {
      path:"/edit/:id",
      name: 'EditBlog',
      component: EditBlog
    }
  ]
})

export default router


