<template>
	<div id="single-blog">
		<h2>{{blog.title}}</h2>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<ul>
			<li v-for="categorie in blog.categories" :key="categorie">{{categorie}}</li>
			<button @click="deleteBlog()">删除此条博客</button>
			<router-link class="editBlog" :to="'/edit/' + id">编辑此条博客</router-link>
		</ul>
	</div>
</template>
<script>
	// import axios from 'axios'
	export default {
		name: 'single-blog',
		data(){
			return {
				blog: {},
				id: this.$route.params.id
			}
		},
		created(){
			// this.$http.get('https://wd2091622437tspsax.wilddogio.com/post/' + this.id + '.json')
			this.axios.get('/post/' + this.id + '.json')		
			.then((response) =>{
				this.blog = response.data
			})
		},
		methods: {
			deleteBlog(){
				this.axios.delete('/post/' + this.id + '.json')
				.then(response =>{
					this.$router.push('/')
				})
			}
		}
	}
</script>
<style scoped>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}
	#single-blog li{
		margin: 20px 0;
		list-style: none;
		font-size: 18px;
		background: #fff;
		padding: 10px 0;
	}
	#single-blog button{
		display: block;
	  margin: 20px auto;
	  background: crimson;
	  color: #fff;
	  border: 0;
	  padding: 14px;
	  border-radius: 4px;
	  font-size: 18px;
	  cursor: pointer;
	}
	#single-blog .editBlog{
		display: inline-block;
		margin: 20px;
		padding: 14px;
		border: 0;
		border-radius: 5px;
		text-decoration: none;
		background: blue;
  		color: #fff;
  		cursor: pointer;
  		font-size: 18px;
	}
</style>


